import React, { useEffect } from 'react'
import styled from 'styled-components'

const Container = styled.div`
  color: white;
  background-image: linear-gradient(rgba(28, 28, 28, 0.7), rgba(28, 28, 28, 0.7)), url('https://images.unsplash.com/photo-1489599849927-2ee91cede3ba?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1170&q=80');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;

  width: 100vw;
  backdrop-filter: blur(10px);
  padding: 0 0 5% 0;
  @media (max-width: 415px) {
    height: auto;
    padding-bottom: 5%;
    padding-top: 18%;
  }
`
const AboutSection = styled.div`
  padding-top: 8%;
  h2{
    font-size: 1.3rem;
    margin-bottom: 2%;
  }
  p{
    font-size: 0.7rem;
  }
  @media (max-width: 415px) {
    h2{
      font-size: 3rem;
    }
    p{
      font-size: 2rem;
    }
  }
`
const LogoSection = styled.div`
  display: grid;
  grid-template-columns: 30% 70%;
  gap: 10%;
  margin-bottom: 5%;
  line-height: 1.5;
  font-size: 0.5rem;
  text-align: justify;
  .IMDB, .tomato, .metacritic{
    width: 100%;
  }
  .tomato, .metacritic{
    background: white;
    border-radius: 8px;
    padding: 10% 5%;
    box-sizing: border-box;
  }
  @media (max-width: 415px) {
    display: block;
    font-size: 1.7rem;
    margin-bottom: 20%;
    text-align: center;
    .IMDB, .tomato, .metacritic{

      width: 35%;
    }
    .tomato, .metacritic{
      padding: 5% 5%;
    }
  }
`
const SubTitle = styled.p`
  fon-size: 0.5rem;
  @media (max-width: 415px) {
    margin-top: 20%;
  }
`
const Wrapper = styled.div`
  max-width: 50%;
  margin: 0 auto;
  margin-top: 5%;
  ${SubTitle} {
    margin-bottom: 5%;
  }
  @media (max-width: 415px) {
    max-width: 80%;
  }
`

export default function AboutPage () {
  useEffect(() => {
    window.scrollTo(0, 0)
  }, [])
  return (
    <Container>
      <AboutSection>
        <h2>About</h2>
        <p>Fun</p>
      </AboutSection>
      <Wrapper>
        <SubTitle></SubTitle>
        <LogoSection>
          <img alt="" className="IMDB" src="https://upload.wikimedia.org/wikipedia/commons/6/69/IMDB_Logo_2016.svg" />
          <p>IMDb110IMDb1250 IMDb</p>
        </LogoSection>
        <LogoSection>
          <img alt="" className="tomato" src="https://upload.wikimedia.org/wikipedia/commons/6/6f/Rotten_Tomatoes_logo.svg" />
          <p>6060</p>
        </LogoSection>
        <LogoSection>
          <img alt="" className="metacritic" src="https://upload.wikimedia.org/wikipedia/commons/4/48/Metacritic_logo.svg" />
          <p>Metacritic</p>
        </LogoSection>
      </Wrapper>
    </Container>

  )
}
